import React, { Component } from 'react';
import ContainerLayout from '../components/containerLayout';
import BlogList from '../components/blogList';
import importBlogs from '../utils/importBlogs'

class Blog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blogs: []
        }
    }

    componentDidMount() {
        importBlogs((blog) => {
            this.setState((state) => { return { blogs: state.blogs.concat([blog]) } })
        });
    }

    render() {
        const { blogs } = this.state;
        return (
            <ContainerLayout>
                <br />
                <h1>Blog</h1>
                <br />
                <BlogList blogs={blogs} />
            </ContainerLayout>
        )
    }
}

export default Blog;