const importBlogs = (setBlogsCallback) => {
    const importAll = (r) => {
        r.keys().forEach(x => {
            var blog = x.substring(2, x.indexOf('.mdx'));

            import(`../pages/blogs/${blog}`).then(res => {
                setBlogsCallback(res)
            })
        });
    }

    return importAll(require.context('../pages/blogs/', true, /\.mdx$/));
}

export default importBlogs;